@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
header {
    background-color: var(--primary);
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 14px;

}

header .nav__links ul{
    list-style-type: none;
}


header .nav__links ul li{
    display: inline-block;
    margin-right: 20px;
}

header .nav__links ul a:hover{
    color: var(--yellow);
    transition: 0.3s ease 0s;
}

header .app__name {
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 20px;
}

header .app__name .ecomm{
    color: var(--yellow);
}

.account__cta a button{
    padding: 10px 30px;
    border: 0;
    margin-right: 5px;
    cursor: pointer;
    font-size: 14px;
    text-transform: uppercase;
}

.account__cta a .login__cta{
    background-color: var(--primary_light);
    color: #fff;
   
}

.account__cta a .login__cta:hover{
    color: var(--yellow);
    transition: 0.3s ease-in-out 0s;
}

.account__cta a .signup__cta{
    background-color: var(--yellow);
    color: var(--primary);
   
}

.account__cta a .signup__cta:hover{
    color: var(--primary_light);
   
}
footer{ 
    padding: 20px;
    background-color: var(--primary);
    color: #c5c5c5;
}

footer .wrapper {
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(350px, 1fr) );
    width: 95%;
    margin: auto;   
    grid-gap: 10px;   
    gap: 10px;
}

footer .f__item {
    padding: 10px;
}

footer .f__item:nth-child(1) p{
    text-align: justify;
    line-height: 1.3125rem;
    max-width: 100%;
}

footer .f__item:nth-child(1) .title{
    margin-bottom: 10px;
}

footer .f__item .title{
    color: #fff;
    font-weight: 500;
}
footer .f__item ul{
    list-style-type: none;
}

footer .f__item ul li{
    margin-top: 10px;
}

footer .f__item ul li a:hover{
    color: var(--yellow);
    transition: 0.3s ease-in-out 0s;
}



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root {
    --primary: #252e38;
    --primary_light: #363e48;
    --yellow: #f1bb14;
}

body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: #252e38;
    color: var(--primary);
    background-color: #fff;
}

a {
    text-decoration: none;
    color: inherit;
}

